    color: #f8f8f8;
    font-size: 1.5rem;
    margin-top: -.5rem;
    text-align: center;
}

.hero p {
    font-size: 13px;
    text-align: center;
}

.hero blockquote {
    text-align: center;
}

.hero button {
    margin: auto;
}

