/* END OF SERVICES CARDS */
.contact-hero {
    width: 100%;
    height: 70vh;
    position: relative;
    background: rgba(0, 0, 0, .4);
}

.contact-hero:before {
    content: '';
    position: absolute;
    background: url(/helping-hands/static/media/contact-bg.a271a999.jpg)no-repeat center center/cover;
    height: 70vh;
    width: 100%;
    z-index: -1;
}

.contact-hero .container {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
}

.contact-hero h1 {
    color: #f8f8f8;
    font-size: 5rem;
}

.contact-hero .content {
    height: 75vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.contact {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: 75vh;
    position: relative;
    background: rgba(255, 255, 255, 0);
}

.contact h1 {
    margin-top: 1rem;
    color: white;
    font-size: 2rem;
}

.map .location-header {
    color: var(--secondary-color);
}


.map {
    margin-top: 2rem;
    margin-left: 12rem;
}

.contact .admission {
    margin-bottom: 5rem;
}

.contact-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-top: 2rem;
    margin-bottom: 2rem;

}

.contact-container form {
    height: 37rem;
    background-color: var(--primary-color);
    border-radius: 1rem;
}

.user-input {
    width: 22rem;
    height: 2rem;
    margin: 2rem;
    display: block;
    padding: 1.5rem;
    border: 2px solid var(--color-bg-variant);
    border-radius: 0.5rem;
}

.email-input {
    height: 10rem;
}

h1 {
    text-align: center;
}

.submit-btn button {
    color: black;
    background-color: rgb(255, 255, 255);
    text-align: center;
    margin-bottom: 2rem;
    text-align: center;
    margin-left: 10rem;
}


  /*  =============================== MODAL ======================================== */

.hide {
    display: none;
    margin-bottom: 2rem;
    color: white;
    text-align: center;
    width: 10rem;
    margin: auto;
    z-index: 2;
}
