    .contact-hero {
        height: 60vh;
    }

    .contact-hero:before {
        height: 60vh;
    }

    .contact-quote {
        margin-top: 10rem;
    } 
    
    .map {
        margin-top: 1rem;
        margin-left: 1rem;
